import styled from "styled-components";
import { css } from "styled-components";
import { useState } from "react";

import form_phone from "../../assets/images/Elements/form_phone.png";

import { A, Text, Title3 } from "../../assets/styles/component";
import CheckBox from "./CheckBox";
import Button from "../../assets/UI/ButtonDesktop";

import { Telegram } from "../../assets/images/Socials/Telegram";

import { EngLoc } from "../../assets/constants/localizations";

const SForm = styled.form`
  display: flex;
  justify-content: space-between;
  gap: 4.375rem;
  padding: 9.375rem 8.3%;
  position: relative;
  overflow: hidden;
  background: #000f67;

  @media (max-width: 1700px) {
    flex-wrap: wrap;
    justify-content: center;
  }

  & button {
    background: #ffffff;
    border-radius: 7.813rem;
    width: fit-content;
    padding: 1.875rem 4.063;
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &::before {
    z-index: 0;
    background: #000f67;
  }

  &::after {
    z-index: 1;
    overflow: hidden;
    background: radial-gradient(
        43.18% 87.18% at 67.39% 25.75%,
        #ff7cda 0%,
        rgba(255, 124, 218, 0) 100%
      ),
      radial-gradient(
        25.12% 79.02% at 32.93% 125.8%,
        #00aee8 0%,
        rgba(0, 174, 232, 0) 100%
      ),
      radial-gradient(
        38.5% 124.42% at 61.09% 172.86%,
        #00aee8 0%,
        rgba(0, 174, 232, 0) 100%
      ),
      radial-gradient(
        60.8% 152.7% at -13.59% 11.65%,
        #00aee8 0%,
        rgba(0, 174, 232, 0) 100%
      ),
      radial-gradient(
        64.16% 160.68% at 112.44% 38.89%,
        #7400e8 0%,
        rgba(116, 0, 232, 0) 100%
      ),
      radial-gradient(
        26.08% 44.57% at 5.86% 120.25%,
        #7400e8 0%,
        rgba(116, 0, 232, 0) 100%
      );
  }

  img {
    width: 38.688rem;
    max-height: 53.563rem;
    z-index: 100;
    transform: translateY(9.375rem);
  }
`;

const P = styled(Text)``;

const FormBox = styled.div`
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: left;

  @media (min-width: 1950px) {
    max-width: 100rem;
  }
`;

const Div = css`
  display: flex;
  flex-direction: column;
`;

const TitleBox = styled.div`
  ${Div}
  gap: 4.375rem;
  margin-bottom: 2.5rem;
`;

const Title = styled(Title3)`
  margin-bottom: 1.5rem;
  font-weight: 400;
  font-size: 6rem;
  line-height: 110%;
  max-width: 60.438rem;
`;

const Fields = css`
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 0.938rem;

  height: 5rem;

  margin-bottom: 0.938rem;
  padding-left: 2.125rem;
  outline: none;
  box-sizing: border-box;

  &::placeholder,
  & {
    font-family: "HeliosExtC", -apple-system, BlinkMacSystemFont, "Segoe UI",
      "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
      "Helvetica Neue", sans-serif;
    font-weight: 400;
    font-size: 1.125rem;
    line-height: 150%;
    color: #fff;
  }

  &:focus {
    outline: 2px solid rgba(255, 255, 255, 0.3);
  }
`;

const InputsBox = styled.div`
  position: relative;
  display: flex;
  flex-direction: row;
  width: 100%;
  margin-bottom: 2.5rem;
  gap: 0.875rem;

  & svg {
    position: absolute;
    opacity: 0.3;
    top: 1.688rem;
    left: 15.625rem;
  }
`;

const Input = styled.input`
  ${Fields}
  min-width: 20.563rem;
  max-width: 25rem;
`;

const TextAreaBox = styled.div`
  position: relative;
  width: 90%;

  & p {
    position: absolute;
    top: 1.625rem;
    left: 205px;
    font-weight: 400;
    font-size: 1.125rem;
    line-height: 150%;
    color: #ffffff66;
  }
`;

const Textarea = styled.textarea`
  ${Fields}
  width: 100%;
  max-width: 36.25rem;
  resize: none;
  padding-top: 1.563rem;

  position: relative;
`;

const ToggleBox = styled.div`
  display: flex;
  gap: 3.75rem;
  margin-bottom: 2.813rem;
`;

const Link = styled(A)`
  color: #fff;
  text-decoration: underline;
  font-weight: 400;
  font-size: 0.75rem;
  line-height: 160%;

  &:hover {
    color: #abe4f7;
  }
`;

const ButtonBox = styled.div`
  display: flex;
  align-items: center;
  gap: 2rem;
  text-align: left;

  p {
    max-width: 21.063rem;
    color: #ffffff99;
  }
`;

const RequestForm = ({ language }) => {
  const [value, setValue] = useState("");
  const [valueTextArea, setValueTextArea] = useState("");

  return (
    <SForm id={"form"}>
      <FormBox>
        <TitleBox>
          <Title>
            {language === "Ru"
              ? "Хотите запустить рекламу?"
              : EngLoc.social_media.form.title}
          </Title>
        </TitleBox>

        <InputsBox>
          {value.length === 0 && <Telegram />}
          <Input
            placeholder={
              language === "Ru"
                ? "Ваш номер или ник"
                : EngLoc.social_media.form.input
            }
            onFocus={(e) => {
              e.target.value == "" && setValue(" ");
              e.target.selectionStart = 0;
              e.target.selectionEnd = 0;
            }}
            onBlur={(e) => {
              e.target.value == " " && setValue("");
            }}
            onChange={(e) => setValue(e.target.value)}
            value={value}
          />

          <TextAreaBox>
            {valueTextArea.length === 0 && (
              <Text>
                {language === "Ru"
                  ? "(не обязательно)"
                  : EngLoc.social_media.form.textarea_after}
              </Text>
            )}

            <Textarea
              placeholder={
                language === "Ru"
                  ? "Опишите задачу"
                  : EngLoc.social_media.form.textarea
              }
              onFocus={(e) => {
                e.target.value == "" && setValueTextArea(" ");
                e.target.selectionStart = 0;
                e.target.selectionEnd = 0;
              }}
              onBlur={(e) => {
                e.target.value == " " && setValueTextArea("");
              }}
              onChange={(e) => setValueTextArea(e.target.value)}
              value={valueTextArea}
            ></Textarea>
          </TextAreaBox>
        </InputsBox>

        <ButtonBox>
          <Button type="secondary" colorTxt="#2850BF">
            {language === "Ru" ? "Запустить рекламу" : EngLoc.buttons.send}
          </Button>
          {language === "Ru" ? (
            <Text size="12">
              Нажимая на кнопку, вы даете согласие на обработку{" "}
              <Link
                targer="_blank"
                href="/privacy"
                aria-label={"Прочитать политику обработки персональных данных"}
              >
                персональных данных
              </Link>{" "}
              и соглашаетесь c{" "}
              <Link
                targer="_blank"
                href="/terms"
                aria-label={"Прочитать политику конфиденциальности"}
              >
                политикой конфиденциальности
              </Link>
            </Text>
          ) : (
            <Text size="12">
              By clicking on the button, you consent to the processing of
              <Link
                targer="_blank"
                href="/privacy"
                aria-label={"Прочитать политику обработки персональных данных"}
              >
                personal data
              </Link>{" "}
              and agree to
              <Link
                targer="_blank"
                href="/terms"
                aria-label={"Прочитать политику конфиденциальности"}
              >
                {" "}
                the privacy policy
              </Link>
            </Text>
          )}
        </ButtonBox>
      </FormBox>
    </SForm>
  );
};

export default RequestForm;
