import styled from "styled-components";
import { EngLoc } from "../../assets/constants/localizations";
import { Title2 } from "../../assets/styles/component";

import RequestForm from "./RequestFormDesktop";
import BrandsList from "./BrandsListDesktop";

const SSocialMedia = styled.section``;

const Title = styled(Title2)`
  margin: 0 8.3%;
  background: linear-gradient(90.45deg, #116ad0 1.65%, #00aee8 114.47%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 120px;

  @media (min-width: 1950px) {
    max-width: 100rem;
    margin: 0 auto;
    margin-bottom: 120px;
  }
`;

const SocialMedia = ({ language }) => {
  return (
    <SSocialMedia>
      <Title color="#3D3D3D">
        {language === "Ru" ? "Мы в СМИ" : EngLoc.social_media.title}
      </Title>
      <BrandsList language={language} />
      <RequestForm language={language} />
    </SSocialMedia>
  );
};

export default SocialMedia;
