import styled from "styled-components";
import { Text } from "../../assets/styles/component";

const SCheckBox = styled.label`
  position: relative;
  display: flex;
  align-items: center;
  gap: 1.25rem;
  height: 2.5rem;
  max-width: 18.75rem;

  padding-left: 4.063rem;
  margin-bottom: 0.75rem;

  font-size: 1.375rem;
  user-select: none;
  cursor: pointer;

  & input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }

  & span {
    position: absolute;
    top: 0;
    left: 0;
    height: 2.5rem;
    width: 2.5rem;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 0.625rem;
  }

  span:after {
    content: "";
    position: absolute;
    opacity: 0;
    transition: 0.1s;
  }

  & input:checked ~ span:after {
    opacity: 1;
  }

  & span:after {
    left: 0.625rem;
    top: 0.625rem;
    width: 1.25rem;
    height: 1.25rem;

    background: linear-gradient(290.71deg, #3cc5ff -23.07%, #ffa4f6 147.25%);
    border-radius: 0.313rem;
  }
`;

const SText = styled(Text)`
  font-size: 1rem;
`;

const CheckBox = ({ name, text }) => {
  return (
    <SCheckBox>
      <SText>{text}</SText>
      <input name="name" type="checkbox" />
      <span></span>
    </SCheckBox>
  );
};

export default CheckBox;
